@charset "UTF-8";
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none; }

body {
  margin: 0; }

.container {
  margin: 0 auto; }

.btn {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
  display: block; }

.btn--green {
  color: #fff;
  background-color: #68b738; }

.btn--transparent {
  color: #000;
  background-color: transparent; }

.header__logo {
  margin: 1px; }

.header__navigation {
  margin: 1px; }

.main-navigation {
  background-color: #fff; }

.main-navigation__list {
  margin: 1px; }

.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px; }

.site-list__item {
  margin: 1px;
  text-align: center;
  box-sizing: border-box;
  padding: 25px 10px;
  border-top: 1px solid #e6e6e6; }

.site-list__item:last-child {
  border-bottom: 1px solid #e6e6e6; }

.site-list__link {
  padding: 25px 10px;
  color: #000; }

.intro {
  color: #fff;
  font-family: "Oswald", "Arial", sans-serif;
  height: 453px;
  background-image: url("../img/intro-mobile.png");
  background-size: contain;
  background-repeat: no-repeat;
  padding-top: 27px; }

.intro__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  padding: 0;
  text-transform: none;
  font-size: 36px;
  line-height: 36px;
  margin-left: 38px; }

.intro__desc {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 14px;
  margin-top: 25px;
  margin-left: 59px; }

.intro__link {
  margin-top: auto; }

.category {
  margin-top: 22px;
  display: block;
  width: 280px; }

.category__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.category__item {
  background-color: #f2f2f2;
  margin-bottom: 20px;
  padding: 30px 21px 9px 20px; }

.category__wrapper {
  border-bottom: 1px solid #e6e6e6;
  padding-right: 14px; }

.category__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 37px;
  text-align: center;
  margin: 0 0 29px; }

.category__slim {
  margin-left: 24px; }

.category__set {
  margin-left: 51px; }

.category__desc {
  font-family: "Arial",sans-serif;
  font-size: 14px;
  line-height: 18px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  padding-bottom: 22px; }

.category__link {
  font-size: 16px;
  padding: 10px 10px 10px 0;
  margin-top: 10px; }

.advantages {
  padding-left: 19px; }

.advantages__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 36px; }

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding: 0 20px 1px 80px;
  margin-top: 35px;
  box-sizing: border-box; }

.advantages__item {
  margin-bottom: 26px; }

.advantages__desc {
  font-family: "Arial", "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px; }

.presentation {
  margin-top: 18px;
  background-color: #f2f2f2;
  box-sizing: border-box;
  padding-top: 17px; }

.presentation__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 36px;
  line-height: 60px;
  margin-left: 18px; }

.presentation__desc {
  font-family: "Arial", "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin-left: 18px;
  margin-top: 23px;
  padding-right: 30px; }

.presentation__result {
  margin-left: 20px; }

.presentation__cost {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  margin: 15px 0 0 0; }

.presentation__slider {
  margin: 0; }

.result {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 280px;
  margin: 0 auto;
  box-sizing: border-box;
  padding-top: 26px; }

.result__wrapper {
  width: 124px;
  text-align: center; }

.result__time,
.result__weight {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  margin: 0px; }

.result__desc {
  font-family: "Arial", "Oswald", sans-serif;
  font-size: 12px;
  line-height: 12px;
  display: inline-block;
  width: 60px;
  margin: 10px 0 0 0; }

.slider {
  height: 362px; }

.slider__slide {
  min-height: 289px; }

.slider__slide--before {
  padding: 1px; }

.slider__slide--after {
  padding: 1px; }

.slider__controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.slider__text {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase; }

.contacts {
  padding: 1px; }

.contacts__desc {
  padding: 0px; }

.contacts__desc--main {
  width: 280px;
  padding: 27px 0 24px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.contacts__invitation {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  width: 140px; }

.contacts__wrapper {
  width: 140px; }

.contacts__adress,
.contacts__city {
  font-family: "Arial", "Oswald", sans-serif;
  font-size: 14px;
  line-height: 20px;
  margin: 0; }

.contacts__map {
  width: 100%;
  min-height: 360px; }

.footer {
  background-color: #f2f2f2; }

.footer__wrapper {
  padding: 1px; }

.footer__social {
  margin: 1px; }

.footer__academy {
  margin: 1px; }

.footer__academy--link {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: #000000; }

.footer__academy--mail {
  position: relative; }

.logo {
  background-color: #000; }

.logo--header {
  width: 100px; }

.logo--footer {
  width: 150px; }

.social {
  padding: 1px;
  list-style: none;
  margin: 0;
  padding: 0; }

.social__item {
  width: 50px;
  height: 50px; }

.social__link {
  background-color: red; }

.social__link--vk {
  background-color: red; }

.social__link--inst {
  background-color: red; }

.social__link--fb {
  background-color: red; }

/*страница каталог*/
.catalog {
  margin: 0px; }

.catalog__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 36px;
  line-height: 60px;
  margin-left: 20px; }

.catalog__list {
  margin: 0; }

.catalog__more-product {
  margin: 0px; }

.cards {
  list-style: none;
  margin: 0;
  padding: 0; }

.cards__item {
  box-sizing: border-box;
  border-top: 1px solid #e6e6e6; }

.cards__item:last-child {
  border-bottom: 1px solid #e6e6e6; }

.card {
  padding: 26px 0 25px 0; }

.card__wrapper {
  width: 280px;
  display: flex;
  justify-content: space-between; }

.card__img {
  width: 143px;
  height: 143px; }

.card__wrapper-table {
  width: 100%; }

.card__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  display: block;
  width: 75px; }

.card__table {
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 19px;
  font-size: 12px;
  line-height: 16px;
  width: 100%; }

.card__rigth {
  text-align: right; }

.card__link {
  margin: 24px; }
